import React, { useEffect } from "react";
import ScrollReveal from "scrollreveal";

import "../styles/main.css";

function ScrollRevealComponent() {
  useEffect(() => {
    // Configurao do Scroll Reveal
    ScrollReveal().reveal(".reveal", {
      delay: 200, // Atraso antes da animao comear em milissegundos
      duration: 1000, // Durao da animao em milissegundos
      distance: "30px", // Distncia que o elemento se mover na animao
      origin: "bottom", // Origem da animao (top, bottom, left, right)
      easing: "ease-out", // Easing function para a animao (pode ser "linear", "ease-in", "ease-out", "ease-in-out")
      reset: true, // Define se a animao deve ser refeita sempre que o elemento se tornar visvel novamente
    });
  }, []);
  return (
    <p
      className="fs-6 text-center corTitulo2 area14 reveal"
      style={{ marginBlockEnd: 60 }}
    >
      Gean-Dev 2023 , Inc
    </p>
  );
}
export default ScrollRevealComponent;
